.about-container {
  background-color: red;
  height: 100vh;
  .about-header {
    background-color: #e7e7e7;
    display: flex;
    align-items: baseline;
    justify-content: center;
    padding-top: 8rem;
    padding-bottom: 4rem;

    p {
      margin-right: 1rem;
      font-size: 20px;
      letter-spacing: 2px;
    }
    h2 {
      font-size: 32px;
      font-weight: 500;
      letter-spacing: 2px;
    }
  }
}
