// full size screens
nav {
  position: fixed;
  z-index: 1;
  width: 100%;
  background-color: white;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  .nav-body {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 0 8%;
    padding: 0.6rem 0;
    img {
      filter: brightness(0);
      width: 9%;
    }
    .nav-links {
      display: flex;
      ul {
        display: flex;
        li {
          font-size: 14px;
          list-style: none;
          margin-right: 4rem;
        }
        li:hover {
          font-weight: 600;
          cursor: pointer;
        }
      }

      button {
        padding: 1rem 2rem;
        border: 2px black solid;
        background-color: white;
        font-size: 15px;
        transition: all 0.3s ease;
      }
      button:hover {
        background-color: black;
        color: white;
        cursor: pointer;
        text-decoration: underline;
        transition: all 0.2s ease;
      }
      align-items: center;
    }
  }
}
